<template>
  <div class="executionInformation">
    <div class="header">
      <el-input
        class="input"
        size="small"
        v-model.trim="queryForm.activityKeyword"
        placeholder=""
      ></el-input>
      <el-button size="small" type="primary" @click="getTableData"></el-button>
    </div>
    <div class="content">
      <!-- <div class="content-title">
          <div class="content-title-left">
            <span class="block"></span>
            <span class="text"></span>
          </div>
      </div>-->
      <el-table
        class="content-table"
        border
        :data="tableData"
        :height="500"
        :row-class-name="tableRowClassName"
      >
        <el-table-column label="" align="center" type="index" width="70"></el-table-column>
        <el-table-column prop="activityName" label=""></el-table-column>
        <el-table-column align="center" label="">
          <template slot-scope="scope">
            <span class="text" type="text" @click="clickCheck(scope.row)"></span>
          </template>
        </el-table-column>
        <el-table-column prop="executePlacePlan" label=""></el-table-column>
        <el-table-column prop="executeTimePlan" label=""></el-table-column>
        <el-table-column prop="updateTime" label=""></el-table-column>
        <el-table-column align="center" label="">
          <template slot-scope="scope">
            <span class="text" type="text" @click="clickEntry(scope.row)"></span>
          </template>
        </el-table-column>
        <el-table-column align="center" width="200" label="">
          <template slot-scope="scope">
            <!-- <span class="text" type="text"></span> -->
            <span class="text" type="text" @click="clickClose(scope.row)"></span>
            <!-- <el-checkbox v-model="scope.row.bindTagB" @change="bindTagBChange(scope.row)"></el-checkbox> -->
          </template>
        </el-table-column>
      </el-table>
      <div class="content-page">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="this.queryForm.current"
          :page-sizes="[10,20,30,40]"
          :page-size="this.queryForm.size"
          layout="total, sizes, prev, pager, next, jumper"
          :total="this.queryForm.total"
        ></el-pagination>
      </div>
    </div>
    <el-dialog
      class="check-dialog"
      title=""
      :visible.sync="checkDialogVisible"
      width="80%"
      :before-close="checkDialogClose"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
    >
      <span slot="title" class="dialog-header">
        <span class="header-text">{{ '' }}</span>
      </span>
      <div class="query-group">
        <div class="search">
          <el-select
            class="select"
            size="small"
            v-model="checkDialogForm.tagId"
            clearable
            placeholder=""
          >
            <el-option
              v-for="item in labelOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
          <el-input
            class="input"
            size="small"
            v-model.trim="checkDialogForm.yhbh"
            placeholder=""
          ></el-input>
          <el-button size="small" type="primary" @click="getCheckDialogData"></el-button>
        </div>
      </div>
      <el-table
        class="dialog-table"
        border
        :data="checkDialogData"
        :height="400"
        :row-class-name="tableRowClassName"
      >
        <el-table-column prop="yhbh" label=""></el-table-column>
        <el-table-column prop="yhmc" label=""></el-table-column>
        <el-table-column prop="yddz" label=""></el-table-column>
        <!-- <el-table-column prop="sstq" label=""></el-table-column> -->
        <!-- <el-table-column prop="tjsx" label=""></el-table-column> -->
      </el-table>
      <div class="dialog-pagination">
        <el-pagination
          @size-change="checkDialogSizeChange"
          @current-change="checkDialogCurrentChange"
          :current-page="this.checkDialogForm.current"
          :page-sizes="[10,20,30,40]"
          :page-size="this.checkDialogForm.size"
          layout="total, sizes, prev, pager, next, jumper"
          :total="this.checkDialogForm.total"
        ></el-pagination>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="checkDialogClose"></el-button>
      </span>
    </el-dialog>
    <el-dialog
      class="entry-dialog"
      title=""
      :visible.sync="entryDialogVisible"
      width="80%"
      :before-close="entryDialogClose"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
    >
      <span slot="title" class="dialog-header">
        <span class="header-text">{{ '' }}</span>
      </span>
      <div class="query-group">
        <div class="search">
          <el-input
            class="input"
            size="small"
            v-model.trim="entryDialogForm.yhbh"
            placeholder=""
          ></el-input>
          <el-button size="small" type="primary" @click="getEntryDialogData"></el-button>
          <el-button size="small" type="primary" @click="downFile"></el-button>
          <el-upload
            class="upload"
            :action="actionUrl"
            :data="uploadParams"
            :on-success="handleSuccess"
            multiple
            accept=".xls, .xlsx"
            :before-upload="beforeUpload"
            :show-file-list="false"
          >
            <el-button size="small" type="primary"></el-button>
          </el-upload>
        </div>
      </div>
      <el-table
        class="dialog-table"
        border
        :data="entryDialogData"
        :height="400"
        :row-class-name="tableRowClassName"
      >
        <el-table-column prop="yhbh" label=""></el-table-column>
        <el-table-column prop="yhmc" label=""></el-table-column>
        <el-table-column prop="yddz" label=""></el-table-column>
<!--        <el-table-column prop="sstq" label=""></el-table-column>-->
        <el-table-column prop="algorithm" label="">
          <template slot-scope="scope">
            {{scope.row.algorithm==="1"?'':''}}
          </template>
        </el-table-column>
        <el-table-column prop="czsj" align="center" label="">
          <template slot-scope="scope">
            <!-- <el-link type="danger">{{scope.name}}</el-link> -->
            <!-- <el-checkbox v-model="scope.row.bindTagB" @change="bindTagBChange(scope.row)"></el-checkbox> -->
            <el-checkbox  v-model="scope.row.flag" :disabled="scope.row.flag" @change="activeSelect(scope.row)"></el-checkbox>
          </template>
        </el-table-column>
        <el-table-column prop="operateTime" label=""></el-table-column>
      </el-table>
      <div class="dialog-pagination">
        <el-pagination
          @size-change="entryDialogSizeChange"
          @current-change="entryDialogCurrentChange"
          :current-page="entryDialogForm.current"
          :page-sizes="[5,20,30,40]"
          :page-size="entryDialogForm.size"
          layout="total, sizes, prev, pager, next, jumper"
          :total="entryDialogForm.total"
        ></el-pagination>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="entryDialogClose"></el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { YDKH_TAG_OBJ } from '@/config'
import Interface from '@/base/mixin/precision-marketing';
import { CP_PATH } from '@/config'
import Qs from 'qs'
export default {
  name: "executionInformation",
  components: {
  },
  mixins: [Interface],
  props: {},
  data() {
    return {
      queryForm: {
        status: "3",
        activityKeyword: "",
        current: 1,
        size: 10,
        total: 0
      },
      // 
      activityId: "",
      tableData: [],
      // 
      checkDialogVisible: false,
      checkDialogData: [{}],
      checkDialogForm: {
        tagId: "",
        yhbh: "",
        current: 1,
        size: 10,
        total: 0
      },
      labelOptions: [],
      // 
      entryDialogVisible: false,
      entryDialogData: [],
      entryDialogForm: {
        yhbh: "",
        current: 1,
        size: 5,
        total: 0
      },
      uploadParams:{
        current: 1,
        size: 5,
        activityId:'',
        fileUploadId:'',
      },
      actionUrl:CP_PATH+'/cptActivityConfig/uploadExcel',
      tableType:'1',//1===2===
    };
  },
  mounted() {
    this.getTableData()
  },
  methods: {
    //
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 === 0) {
        return 'success-row';
      }
      return '';
    },
    // 
    handleSizeChange(val) {
      this.queryForm.size = val
      this.getTableData()
    },
    handleCurrentChange(val) {
      this.queryForm.current = val
      this.getTableData()
    },
    // 
    clickEntry(row) {
      this.activityId = row.activityId
      window.console.log(row,';;')
      this.getEntryDialogData()
      this.entryDialogVisible = true
    },
    // 
    clickCheck(row) {
      this.activityId = row.activityId
      this.checkDialogForm.current = 1
      this.checkDialogForm.size = 10
      this.getLabelOptions()
      this.getCheckDialogData()
      this.checkDialogVisible = true
    },
    // 
    checkDialogClose() {
      this.checkDialogVisible = false
    },
    // 
    checkDialogSizeChange(val) {
      this.checkDialogForm.size = val
      this.getCheckDialogData()
    },
    checkDialogCurrentChange(val) {
      this.checkDialogForm.current = val
      this.getCheckDialogData()
    },
    //
    downFile() {
      window.location.href = CP_PATH + '/cptActivityConfig/downloadTemplate'
    },
    //
    beforeUpload(file, fileList) {
      this.uploadParams.activityId = this.activityId
      this.uploadParams.current = this.entryDialogForm.current = 1
      this.uploadParams.size = this.entryDialogForm.size
      var testmsg = file.name.substring(file.name.lastIndexOf('.') + 1)
      let isType = (testmsg === 'xls' || testmsg === 'xlsx')
      if (!isType) {
        this.$alert(`xlsxlsx!`);
      }
      return isType;
    },
    handleSuccess(res,file) {
      this.uploadParams.fileUploadId = res.data.fileUploadId||''
      console.log(file);
      if (res.code === "00000") {
        this.tableType = '2'
        this.$message.success('')
        res.data.rows.forEach((v)=>{
          v.flag = v.flag==='1'?true:false
        })
        this.entryDialogData = [...res.data.rows]
        this.entryDialogForm.total = res.data.total
        this.entryDialogData.forEach(e => {
          e.bindTagB = e.bindTag === '1' ? true : false
        })
      }
    },
    //
    UploadExcel(){
      this.uploadExcel(Qs.stringify({
        current: this.entryDialogForm.current,
        size: this.entryDialogForm.size,
        activityId:  this.uploadParams.activityId,
        fileUploadId: this.uploadParams.fileUploadId,
      })).then(res=>{
        this.tableType = '2'
        res.rows.forEach((v)=>{
          v.flag = v.flag==='1'?true:false
        })
        this.entryDialogData = [...res.rows]
        this.entryDialogForm.total = res.total
        this.entryDialogData.forEach(e => {
          e.bindTagB = e.bindTag === '1' ? true : false
        })
      })
    },
    // 
    entryDialogClose() {
      this.entryDialogVisible = false
    },
    // 
    entryDialogSizeChange(val) {
      this.entryDialogForm.size = val
      if (this.tableType==='1'){
        this.getEntryDialogData()
      }else {
        this.UploadExcel()
      }
    },
    entryDialogCurrentChange(val) {
      this.entryDialogForm.current = val
      if (this.tableType==='1'){
        this.getEntryDialogData()
      }else {
        this.UploadExcel()
      }
    },
    // 
    clickClose(row) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        window.console.log(row)
        this.closeTask({
          "id":row.id
        }).then(()=>{
          this.getTableData()
          this.$message.info('')
        })
      }).catch(() => {
      })
    },
    // 
    getTableData() {
      this.getActivityConfig(this.queryForm).then(res => {
        this.tableData = [...res.records]
        this.queryForm.total = res.total
      })
    },
    // 
    getCheckDialogData() {
      this.getRecommendCustomer({ ...this.checkDialogForm, activityId: this.activityId }).then(res => {
        this.checkDialogData = [...res.records]
        this.checkDialogForm.total = res.total
      })
    },
    // 
    getEntryDialogData() {
      this.customerList({
        size:this.entryDialogForm.size,
        current:this.entryDialogForm.current,
        "activityId":this.activityId,
        "yhbh":this.entryDialogForm.yhbh,
      }).then(res=>{
        this.tableType = '1'
         res.records.forEach((v)=>{
          v.flag = v.flag==='1'?true:false
        })
        this.entryDialogData = res.records
        window.console.log(this.entryDialogData)
        this.entryDialogForm.total = res.total
      })
    },
    // 
    getLabelOptions() {
      this.getLabel({
        tagobjId: YDKH_TAG_OBJ,
      }).then(res => {
        if (res.length !== 0) this.labelOptions = res.map(e => {
          return {
            value: e.bqId,
            label: e.bqName,
          }
        })
      })
    },
    //
    activeSelect(row){
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.joinActivity({
          "activityId":this.activityId,
          "yhbh":row.yhbh
        }).then(()=>{
          this.$message.success('')
        })
      }).catch(() => {
        row.flag = false;
        this.$message({
          type: 'info',
          message: ''
        });
      });
    }
  }
};
</script>

<style scoped lang="scss">
.executionInformation {
  ::v-deep .el-table .warning-row {
    // background: #e7f6f7;
    background: #e9f3fe;
  }
  ::v-deep .el-table {
    .success-row {
      background: rgb(244, 244, 244);
    }
    .cell {
      padding: 2px 4px;
    }
    th {
      background-color: rgb(24, 153, 250);
      .cell {
        color: #ffffff;
      }
    }
  }
  .content-border {
    background: #ffffff;
    border: 2px solid rgba(26, 132, 244, 0.2);
    border-radius: 4px;
  }
  .header {
    height: 40px;
    display: flex;
    align-items: center;
    .select {
      margin-right: 20px;
    }
    .input {
      width: 300px;
      margin-right: 20px;
    }
  }
  .content {
    .content-table {
      width: 100%;
      // height: 350px;
      .text {
        color: #1899fa;
        cursor: pointer;
      }
    }
    .content-page {
      margin-top: 10px;
      width: 100%;
      text-align: center;
    }
  }
  .check-dialog,
  .entry-dialog {
    ::v-deep .el-table .warning-row {
      background: #e9f3fe;
    }
    ::v-deep .el-dialog__header {
      padding: 10px 0 10px;
      border-bottom: 1px solid #e6e6e6;
    }
    ::v-deep .el-dialog__body {
      padding: 10px;
    }
    ::v-deep .el-dialog__footer {
      padding: 10px 10px 10px 0;
      border-top: 1px solid #e6e6e6;
      text-align: center;
    }
    .dialog-header {
      .header-text {
        margin-left: 20px;
        color: #808080;
        font-size: 14px;
      }
    }
    .query-group {
      margin-bottom: 10px;
      .search {
        display: flex;
        align-items: center;
      }
      .select {
        margin-right: 20px;
      }
      .input {
        width: 300px;
        margin-right: 20px;
      }
      .upload {
        margin: 0 10px;
      }
    }

    .dialog-pagination {
      text-align: center;
      margin: 10px 0;
    }
  }
}
</style>
