/**
 *  - mixin module
 *  @module base/mixin/commonApi
 *  @author coder
 */
import {
  getPowerAreaCode,
  getBdaTagList,
  getXl
} from '@/base/api/common-api'

// mixin
export default {

  /**
   * -
   * @member commonApi
   * @property {Array} [list=[]] -
   * @property {number} [page=null] -
   * @property {number} [limit=null] -
   * @property {number} [total=0] -
   * @property {object} [model=null] -
   */
  data() {
    return {
      commonApi: {
        list: [],
        page: null,
        limit: null,
        total: 0,
        model: null
      }
    }
  },
  methods: {

    /**
     * -
     * @function getPowerAreaCode
     * @param {object} data data
     * @returns {Promise} Promise
     */
    getPowerAreaCode(data) {
      return getPowerAreaCode(data).then(res => {
        return res
      })
    },
    /**
     * --
     * @function getBdaTagList
     * @param {object} data data
     * @returns {Promise} Promise
     */
    getBdaTagList(data) {
      return getBdaTagList(data).then(res => {
        return res
      })
    },
    /**
     * list
     * @function getXl
     * @param {object} data data
     * @returns {Promise} Promise
     */
    getXl(data) {
      return getXl(data).then(res => {
        return res
      })
    }
  }
}
